import fs from 'fs/promises'
import path from 'path'
import { app } from 'electron'

class StorageService {
  private dataPath: string

  constructor() {
    this.dataPath = path.join(app.getPath('userData'), 'deck-data.json')
    console.log('Storage path:', this.dataPath)
  }

  async saveDeck(data: { cards: any[], name: string }) {
    try {
      // Save only necessary card fields to prevent large file sizes and ensure proper restoration
      const sanitizedData = {
        name: data.name,
        cards: data.cards.map(card => ({
          id: card.id,
          word: card.word,
          definition: card.definition,
          audioSource: card.audioSource,
          audioRegion: card.audioRegion,
          createdAt: card.createdAt,
          updatedAt: card.updatedAt
        }))
      }
      
      console.log('Saving deck:', {
        path: this.dataPath,
        cardCount: data.cards.length,
        name: data.name
      })
      
      await fs.writeFile(this.dataPath, JSON.stringify(sanitizedData, null, 2))
      return true
    } catch (error) {
      console.error('Error saving deck:', error)
      throw error // Propagate error to renderer for better error handling
    }
  }

  async loadDeck() {
    try {
      console.log('Loading deck from:', this.dataPath)
      const data = await fs.readFile(this.dataPath, 'utf-8')
      const parsedData = JSON.parse(data)
      console.log('Loaded deck:', {
        cardCount: parsedData.cards?.length,
        name: parsedData.name
      })
      return parsedData
    } catch (error) {
      if ((error as NodeJS.ErrnoException).code === 'ENOENT') {
        console.log('No saved deck found, returning default state')
        return { cards: [], name: 'AnkiBee Deck' }
      }
      console.error('Error loading deck:', error)
      return null
    }
  }
}

export const storageService = new StorageService() 